body {
  font: 14px/1.71 "Roboto", sans-serif;
  letter-spacing: 0.03em;
  color: #757575;
  background-color: #fff;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  margin-inline: auto;
  padding-inline: 15px;
}
@media (min-width: 480px) {
  .container {
    width: 480px;
  }
}
@media (min-width: 768px) {
  .container {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.section {
  padding-block: 60px;
}
@media (min-width: 1200px) {
  .section {
    padding-block: 94px;
  }
}
.section__title {
  font-weight: 700;
  font-size: 28px;
  line-height: 1.18;
  letter-spacing: 0.03em;
  text-align: center;
  color: #212121;
  margin-bottom: 30px;
}
@media (min-width: 1200px) {
  .section__title {
    font-size: 36px;
    line-height: 1.17;
    margin-bottom: 50px;
  }
}

.header {
  font-weight: 500;
  font-size: inherit;
  line-height: 1.14;
  letter-spacing: 0.02em;
  border-bottom: 1px solid #ececec;
}

.header__box {
  display: flex;
  align-items: center;
}

.header__link {
  display: inline-flex;
  align-items: center;
  color: currentColor;
  fill: currentColor;
  padding: 32px 0;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.header__link:hover, .header__link:focus {
  color: #2196f3;
}

.header__link:not(:last-child) {
  margin-right: 30px;
}

.header__icon {
  margin-right: 10px;
}

.nav {
  display: flex;
  margin-right: auto;
  align-items: center;
}

.nav__list {
  display: flex;
}

.nav__item + .nav__item {
  margin-left: 50px;
}

.nav__link {
  position: relative;
  display: block;
  color: #212121;
  padding: 32px 0;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.nav__link--current,
.nav__link:hover,
.nav__link:focus {
  color: #2196f3;
}

.nav__link::after {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #2196f3;
  border-radius: 2px;
  transform: scaleX(0);
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.nav__link:hover::after,
.nav__link:focus::after,
.nav__link--current::after {
  transform: scaleX(1);
}

.logo {
  display: inline-block;
  font: 700 24px/1.17 "Raleway", sans-serif;
  color: #2196f3;
}
@media (min-width: 1200px) {
  .logo {
    font-size: 26px;
    line-height: 1.19;
  }
}
.footer-contacts > .logo {
  margin-bottom: 20px;
}
.logo--dark {
  color: #000;
}
.logo--light {
  color: #fff;
}

.logo__nav {
  padding-top: 24px;
  padding-bottom: 25px;
  margin-right: 93px;
}

