.hero__title {
  margin-bottom: 30px;
  font-weight: 900;
  font-size: 26px;
  line-height: 1.62;
  letter-spacing: 0.06em;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
}
@media (min-width: 1200px) {
  .hero__title {
    font-size: 44px;
    line-height: 1.36;
  }
}

.button {
  display: block;
  margin-inline: auto;
  padding: 10px 32px;
  min-width: 200px;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.88;
  letter-spacing: 0.06em;
  color: #fff;
  background-color: #2196f3;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.button:hover, .button:focus {
  background-color: #188ce8;
}

.button-filter {
  padding: 6px 22px;
  font-family: inherit;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.62;
  text-align: center;
  letter-spacing: 0.03em;
  color: #212121;
  background-color: #f5f4fa;
  border: none;
  border-radius: 4px;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.button-filter:hover,
.button-filter:focus,
.button-current {
  color: #fff;
  background-color: #2196f3;
  box-shadow: 0px 3px 1px rgba(0, 0, 0, 0.1), 0px 1px 2px rgba(0, 0, 0, 0.08), 0px 2px 2px rgba(0, 0, 0, 0.12);
  cursor: pointer;
}

@media (min-width: 768px) {
  .features {
    display: flex;
    flex-wrap: wrap;
    margin: -15px;
  }
}

@media (max-width: 767px) {
  .features__item:not(:last-child) {
    margin-bottom: 30px;
  }
}
@media (min-width: 768px) {
  .features__item {
    margin: 15px;
    flex-basis: calc((100% - 60px) / 2);
  }
}
@media (min-width: 1200px) {
  .features__item {
    flex-basis: calc((100% - 120px) / 4);
  }
}

.features__thumb {
  display: inline-flex;
  justify-content: center;
  width: 100%;
  height: 120px;
  margin-bottom: 30px;
  background-color: #f5f4fa;
  border-radius: 4px;
}

.features__title {
  margin-bottom: 10px;
  font-size: 14px;
  line-height: 1.14;
  text-transform: uppercase;
  color: #212121;
}
@media (max-width: 767px) {
  .features__title {
    text-align: center;
  }
}

.work {
  padding-top: 0;
}
@media (max-width: 1199px) {
  .work {
    display: none;
  }
}

.work__list {
  display: flex;
  margin: -15px;
}

.work__item {
  margin: 15px;
  flex-basis: calc((100% - 90px) / 3);
}

.work__link {
  position: relative;
  display: block;
}

.work__description {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 27px 15px;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.14;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: rgba(47, 48, 58, 0.8);
}

.team {
  background-color: #f5f4fa;
}

@media (min-width: 768px) {
  .team__list {
    display: flex;
    flex-wrap: wrap;
    margin: -15px;
  }
}

.team__item {
  background-color: #fff;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 0px 0px 4px 4px;
}
@media (max-width: 767px) {
  .team__item:not(:last-child) {
    margin-bottom: 30px;
  }
}
@media (min-width: 768px) {
  .team__item {
    margin: 15px;
    flex-basis: calc((100% - 60px) / 2);
  }
}
@media (min-width: 1200px) {
  .team__item {
    flex-basis: calc((100% - 120px) / 4);
  }
}

.team__box {
  padding: 30px 32px;
  text-align: center;
}

.team__title {
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.19;
  letter-spacing: 0.03em;
  color: #212121;
}

.team__text {
  margin-bottom: 16px;
  font-size: 16px;
  line-height: 1.19;
  letter-spacing: 0.03em;
}

.social-links {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.social-links__link {
  fill: #afb1b8;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.social-links__link--footer {
  fill: #fff;
  background-color: rgba(255, 255, 255, 0.1);
}

.social-links__link:hover,
.social-links__link:focus {
  fill: #fff;
  background-color: #2196f3;
}

.clients {
  display: flex;
  flex-wrap: wrap;
  margin: -15px;
}

.clients__item {
  margin: 15px;
}
@media (max-width: 767px) {
  .clients__item {
    flex-basis: calc((100% - 60px) / 2);
  }
}
@media (min-width: 768px) {
  .clients__item {
    flex-basis: calc((100% - 90px) / 3);
  }
}
@media (min-width: 1200px) {
  .clients__item {
    flex-basis: calc((100% - 180px) / 6);
  }
}

.clients__link {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 92px;
  border: 1px solid #afb1b8;
  border-radius: 4px;
  fill: #afb1b8;
  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1), border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.clients__link:hover, .clients__link:focus {
  fill: #2196f3;
  border-color: #2196f3;
}

.footer {
  padding-block: 60px;
  background-color: #2f303a;
}
@media (max-width: 1199px) {
  .footer {
    text-align: center;
  }
}

@media (min-width: 1200px) {
  .footer-box {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
  }
}

@media (min-width: 768px) {
  .footer-tablet-wrapper {
    display: flex;
    align-items: baseline;
  }
}
@media (min-width: 768px) and (max-width: 1199px) {
  .footer-tablet-wrapper {
    justify-content: space-around;
    margin-bottom: 60px;
  }
}

@media (max-width: 767px) {
  .footer-contacts {
    margin-bottom: 60px;
  }
}

.address {
  font-style: normal;
  color: #fff;
}

.address__link {
  display: block;
  margin-top: 9px;
  color: rgba(255, 255, 255, 0.6);
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.address__link:hover,
.address__link:focus {
  color: rgba(33, 150, 243, 0.6);
}

@media (min-width: 480px) and (max-width: 1199px) {
  .newsletter {
    width: 450px;
    margin-inline: auto;
  }
}

@media (min-width: 1200px) {
  .newsletter-form {
    display: inline-flex;
    align-items: center;
    gap: 12px;
  }
}

.newsletter-form > label {
  display: block;
}
@media (max-width: 1199px) {
  .newsletter-form > label {
    margin-bottom: 20px;
  }
}
@media (min-width: 1200px) {
  .newsletter-form > label {
    width: 358px;
  }
}

.singup-form__input {
  width: 100%;
  padding: 15px 16px;
  font-size: 16px;
  letter-spacing: 0.03em;
  color: #fff;
  background-color: inherit;
  border: 1px solid rgba(255, 255, 255, 0.3);
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.15));
  border-radius: 4px;
  outline: none;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.singup-form__input::placeholder {
  line-height: 1.25;
  color: rgba(255, 255, 255, 0.6);
}

.singup-form__input:hover,
.singup-form__input:focus,
.singup-form__input:not(:placeholder-shown):required {
  cursor: pointer;
  border-color: #188ce8;
}

.newsletter-form__button {
  display: inline-flex;
  align-items: center;
  gap: 10px;
}

@media (max-width: 767px) {
  .call-to-action {
    margin-bottom: 60px;
  }
}
@media (min-width: 1200px) {
  .call-to-action {
    margin-left: 70px;
  }
}

.action-title {
  margin-bottom: 20px;
  font-weight: 700;
  line-height: 1.14;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: #fff;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  background-color: rgba(0, 0, 0, 0.2);
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.backdrop.is-hidden > .modal {
  transform: translate(-50%, -50%) scale(1.2);
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
  min-width: 528px;
  min-height: 581px;
  padding: 40px;
  border-radius: 4px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
  background-color: #fff;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.button-close {
  position: absolute;
  top: 0px;
  right: 0px;
  padding: 6px;
  transform: translate(32%, -32%);
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  background-color: #fff;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
}

.button-close:hover,
.button-close:focus {
  fill: #2196f3;
}

.modal__svg {
  display: block;
}

.modal__title {
  margin-bottom: 28px;
  font-weight: 700;
  font-size: 20px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.03em;
  color: #212121;
}

.form-field {
  position: relative;
  display: block;
  margin-bottom: 28px;
  cursor: pointer;
}

.form-field > input {
  width: 100%;
  padding: 11px 12px 11px 42px;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;
  outline: none;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
}

.form-field > span {
  position: absolute;
  top: 50%;
  left: 42px;
  transform: translateY(-50%);
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  font-size: 12px;
  line-height: 1.17;
  letter-spacing: 0.01em;
}

.form-field:focus-within > span,
.form-field > input:not(:placeholder-shown) + span {
  transform: translate(-30px, -38px);
}

.form-field > svg {
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.form-field:focus-within > input {
  border-color: #2196f3;
}

.form-field:focus-within {
  fill: #2196f3;
}

.form-comment {
  position: relative;
  display: block;
  margin-bottom: 20px;
}

.form-comment > textarea {
  display: block;
  width: 100%;
  height: 120px;
  padding: 12px 16px;
  resize: none;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.form-comment > label {
  position: absolute;
  top: 12px;
  left: 16px;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  font-size: 12px;
  line-height: 1.17;
  letter-spacing: 0.01em;
  cursor: pointer;
}

.form-comment:focus-within > textarea {
  border-color: #2196f3;
}

.form-comment:focus-within > label,
.form-comment > textarea:not(:placeholder-shown) + label {
  transform: translateY(calc(-200% - 2px));
}

.form-checkbox {
  display: flex;
  justify-content: center;
  cursor: pointer;
  column-gap: 5px;
  margin-bottom: 30px;
}

.form-checkbox input {
  position: absolute;
  appearance: none;
}

.form-checkbox span {
  display: inline-flex;
  align-items: center;
}

.form-checkbox span::before {
  content: "";
  display: inline-block;
  width: 16px;
  height: 15px;
  margin-right: 7px;
  border: 2px solid #212121;
  border-radius: 2px;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.form-checkbox:hover > span::before,
.form-checkbox > input:focus + span::before {
  border-color: #2196f3;
}

.form-checkbox > input:checked + span::before {
  background-image: url("../images/icon-check.svg");
  background-color: #2196f3;
  border-color: #2196f3;
  background-size: contain;
  background-position: center;
  background-origin: border-box;
  background-repeat: no-repeat;
}

.form-checkbox a {
  text-decoration: underline;
  color: #2196f3;
}

.filter {
  display: flex;
  justify-content: center;
  margin-bottom: 50px;
}

.filter__item:not(:last-child) {
  margin-right: 8px;
}

.card-set {
  display: flex;
  flex-wrap: wrap;
  margin-top: -30px;
  margin-left: -30px;
}

.card-set__item {
  flex-basis: calc(33.3333333333% - 30px);
  margin-top: 30px;
  margin-left: 30px;
}

.card {
  display: block;
  color: inherit;
}

.card:hover,
.card:focus {
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.6), 1px 4px 6px rgba(0, 0, 0, 0.16);
}

.card:hover .card__overlay,
.card:focus .card__overlay {
  transform: translateY(0);
  opacity: 1;
}

.card__thumb {
  position: relative;
  overflow: hidden;
}

.card__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: translateY(101%);
  opacity: 0;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  background-color: rgba(33, 150, 243, 0.9);
}

.card__description {
  padding: 63px 24px;
  font-size: 18px;
  line-height: 1.56;
  color: #fff;
}

.card__box {
  padding: 20px 24px;
  border: 1px solid #eeeeee;
}

.card__title {
  margin-bottom: 4px;
  font-size: 18px;
